
import React from 'react'

export default function  ComParametro(props) {
    const status = props.nota >= 7 ? 'aprovado' : 'Recuperação'
    return (
        <div>
            <h2>{props.Titulo}</h2>
            <h3>
                <strong>{props.aluno} </strong> tem nota:  <strong>{props.nota}</strong> e está <strong>{status}!</strong>
          </h3>
        </div>
    )
}