import './index.css'
import ReactDOM from 'react-dom'  // importa uma biblioteca necessaria para utilizar o react, está dentro de node modules
import React from 'react' // importa um biblioteca que possibilita utilizar o JSX


import App from './app'


//JSX é um codigo javascript com aparencia de html e é usado dentro do render

const el = document.getElementById('root')

ReactDOM.render(     // o render renderiza dentro de uma div desejada ('texto desejado', div)
    
           <App/>,     // isso é JSX
          el  ) 

          